import { useEffect } from 'react';

//Component imports
import Background from '../components/Background/Background';

//Library imports
import AOS from 'aos';

//Style imports
import 'aos/src/sass/aos.scss';
import './styles/index.scss';

export default () => {
  let mobile = false;

  useEffect(() => {
    AOS.init({offset: 10});
    mobile = window.innerWidth < 768;
    window.addEventListener('resize', () => {mobile = window.innerWidth < 768;});

    setInterval(() => {
      mobile != mobile
    }, 400)
  },[]);

  const current = 0;
  

  return (
    <div id='main-wrapper'>
      <Background mobile={mobile} active={current !== 1} />
    </div>
  )
}
  
